	module.exports = {"data":{"markdownRemark":{"html":"<p>概要：英语学习</p>\n<ul>\n<li>单词</li>\n<li>GRE | 托福</li>\n<li>VOA, BBC</li>\n</ul>\n<p><strong>挑战是设计出来的，不断为自己设计必要难度的挑战。</strong></p>\n<h3>精进</h3>\n<ul>\n<li>努力不是一味的用力，而是一种具有策略性的活动，可以不断学习和优化。</li>\n<li>资源有限的情况下，集中资源，投入发展一项优势才能。</li>\n<li>把时间主要投放在一个领域，以尽量高的标准要求自己，培养高才能。</li>\n<li>用非常少的时间广泛涉猎，以捕捉和发展未曾预料的资源和机会。</li>\n<li>对专业追求完美，其它只需追求八分，把一件事做到极致。</li>\n<li>专注在一个领域走到最后。</li>\n<li>到底应该怎样努力，应该采用什么样的策略来投入自己的时间和精力，应该怎样有效的启动，维持，优化对一件事情的投入。</li>\n<li>构建个人的挑战阶梯。</li>\n<li>最多关注3-5个领域的信息。</li>\n<li>选取3个最有用的信息来源，以及原因。</li>\n<li>在确定关注的领域中，最近最值得你关注的是什么。</li>\n<li>不断优化你的努力方式。</li>\n<li>乔哈里之窗。</li>\n</ul>","frontmatter":{"date":"February 23, 2018","path":"/blog/weekly/1","title":"前端每周进阶 第1期"}}},"pathContext":{}}

